import * as React from "react";
import { Component } from "react";

import { OpponentInterface } from "utils/types";

interface Props {
  opponent: OpponentInterface;
  isPlayer: boolean;
}

class OpponentHUD extends Component<Props> {
  render() {
    const { opponent, isPlayer } = this.props;

    return (
      <div
        className={`OpponentHUD ${isPlayer ? "player" : "enemy"}`}
      >
        <div className="head">
          <div className="head-img" />
          <h1 className="name">{opponent.name}</h1>
        </div>
        <HealthBar opponent={opponent} isPlayer={isPlayer} />
        <style jsx>{`
          .OpponentHUD {
            display: flex;
            align-items: center;
          }

          .OpponentHUD.enemy {
            flex-direction: row-reverse;
          }

          .OpponentHUD .head {
            display: flex;
            margin: 0 1rem 0 0;
            flex-direction: column;
            align-items: center;
          }

          .OpponentHUD.enemy .head {
            margin: 0 0 0 1rem;
          }

          .OpponentHUD .head .head-img {
            background-image: url(/static/img/${this.props.opponent.srcName}/head.png);
            background-size: 90% 90%;
            background-repeat: no-repeat;
            background-position: center center;
            background-color: #a00;
            width: 4rem;
            height: 4rem;
            padding: 1rem;
            border-radius: 50%;
          }

          .OpponentHUD.enemy .head .head-img {
            transform: scaleX(-1);
          }

          .OpponentHUD .head h1.name {
            display: block;
            font-size: 1.2rem;
            line-height: 1.5rem;
            text-align: center;
            width: 4rem;
          }
        `}</style>
      </div>
    );
  }
}

export default OpponentHUD;
